<template>

    <!--  -->
    <div class="goods-list">
        
        <!--  -->
        <!--
        1.a 
        2.window.location.herf -->
        <!-- <router-link class="goods-item" v-for="item in goodsList" :key="item.id" 
        :to="'/home/goodsinfo/'+item.id" tag="div">
            <img :src="item.img_url" alt="">
            <h1 class="title">{{item.title}}</h1>
            <div class="info">
                <p class="price">
                    <span class="now">{{item.sell_price}}</span>
                    <span class="old">{{item.market_price}}</span>
                </p>
                <p class="sale">
                    <span></span>
                    <span>{{item.stock_quantity}}</span>
                </p>
            </div>
        </router-link> -->

        <div class="goods-item" v-for="item in goodsList" :key="item.id" @click="goDetail(item.id)">
            <img :src="item.img_url" alt="">
            <h1 class="title">{{item.title}}</h1>
            <div class="info">
                <p class="price">
                    <span class="now">{{item.sell_price}}</span>
                    <span class="old">{{item.market_price}}</span>
                </p>
                <p class="sale">
                    <span></span>
                    <span>{{item.stock_quantity}}</span>
                </p>
            </div>
        </div>

        <!--  -->
        <transition>
        <button type="button" class="mui-btn mui-btn-outlined mui-btn-primary" 
            style='width:100%' v-if="more_show" @click="getmoreList" :key="'more'">
            
        </button>
        <button type="button" class="mui-btn mui-btn-outlined mui-btn-primary mui-disabled" 
        disabled="" style='width:100%' v-else="more_show"><span class="mui-spinner" :key="'loading'">
            </span>&nbsp;<span>Loading...</span> 
            </button>
        </transition>

    </div>
</template>

<script>

import { Toast } from 'mint-ui'

export default {
    name:"goodList",
    data(){
        return{
            pageIndex: 1,
            goodsList: [],
            more_show: true,
        }
    },
    created(){
        this.getGoodsList();
    },
    methods:{
        /* getGoodsList(){
            this.$http.get('api/getgoods?pageindex='+this.pageIndex).then(
                result => {
                    if(result.body.status === 0){
                        // console.log(result.body.message);   
                        this.goodsList = this.goodsList.concat(result.body.message);
                        this.more_show = true;

                    }else{
                        Toast("");
                        this.more_show = true;
                    }
                })
        }, */
        async getGoodsList(){
            const {data} = await this.$http.get('api/getgoods?pageindex='+this.pageIndex);
            if(data.status === 0){
                this.goodsList = this.goodsList.concat(data.message);
                this.more_show = true;

            }else{
                Toast("");
                this.more_show = true;
            }
        },

        getmoreList(){
            this.pageIndex++;
            this.more_show = false;
            this.getGoodsList();
            // setInterval( ()=>{ this.more_show = true;}, 1000 );
        },
        goDetail(id){
            // console.log(this);
            this.$router.push('/home/goodsinfo/'+id);
        }
    }
}
</script>


<style lang="less" scoped>

.goods-list{
    display: flex;
    flex-wrap: wrap;
    padding: 7px;
    //
    justify-content:space-between;//space-arround

    .goods-item{
        width: 49%;
        border: 1px solid #ccc;
        box-shadow:0 0 8px #ccc;
        border-radius: 6px;
        //
        margin:4px 0;
        padding: 2px;
        //
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        /*relativeinfoabsolute
         padding*/
        // position: relative;
        // padding-bottom: 6px;

        //
        min-height: 293px;

        img{
            width:100%;
        }
        .title{
            font-size: 15px;
        }
        .info{
            background-color: #eee;
            // infoabsolute
            // position: absolute;
            // width: 100%;
            // bottom: 0;

            p{
                margin: 0;
                padding: 4px;
            }
            .price{
                .now{
                    color:red;
                    font-size: 16px;
                    font-weight:bold;
                }
                .old{
                    text-decoration: line-through;//
                    font-size: 12px;
                    margin-left: 10px;
                }
            }
            .sale{
                display: flex;
                justify-content: space-between;
                font-size: 13px
            }
        }
    }
}

//
.v-enter, .v-leave-to{
    opacity: 0;
    position:relative;
}

.v-enter-active, .v-leave-active{
    transition: opacity 0.3s;
}


// 
// relativeinfoabsolute
// 

</style>



