<template>
    <div>
        <div id="slider" class="mui-slider" >
            <div id="sliderSegmentedControl" class="mui-scroll-wrapper mui-slider-indicator mui-segmented-control mui-segmented-control-inverted">
                <div class="mui-scroll">
                    <!-- 
                    id=0 -->
                    <a :class="['mui-control-item',  item.id==0?'mui-active':'']" 
                    v-for="item in Categorys" :key="item.id" @click="getPhotoList(item.id)">
                        {{item.title}}
   
                    </a>
                </div>
            </div>      

         <!--  -->
         <div>
            <ul class="photo-list">
                <router-link v-for="item in photoList" :key="item.id" :to="'/home/photoinfo/'+item.id" tag='li' >
                    <img v-lazy="item.img_url">
                 <!-- -->
                    <div class="info">
                        <h1 class="info-title">{{ item.title }}</h1>
                        <div class="info-body">{{ item.zhaiyao }}</div>
                    </div>                    
                </router-link> 
  
            </ul>
        </div>
        </div>
       
        


    </div>
</template>

<script>

import mui from '../../lib/mui/js/mui.min.js'
import { Toast } from 'mint-ui'



export default {
    name:'photoList',
    data(){
        return{
            Categorys: [],
            photoList: []
        }
    },
    created(){
        this.getAllCategory();
        this.getPhotoList(0);
    },
    mounted(){
        mui('.mui-scroll-wrapper').scroll({
            deceleration: 0.0005 //flick 0.0006
        });   
    },
    methods:{
        /* getAllCategory(){
            this.$http.get('api/getimgcategory').then( result => {
                if(result.data.status === 0){
                     result.data.message.unshift({title:"", id:0});
                    //  console.log(result.body.message);
                     this.Categorys = result.data.message;
                } else {
                    Toast('');
                }
            })
        }, */
        async getAllCategory(){
            const {data} = await this.$http.get('api/getimgcategory');
            if(data.status === 0){
                data.message.unshift({title:"", id:0});
                this.Categorys = data.message;
            } else {
                Toast('');
            }
    },

        /* getPhotoList(cateId){
            this.$http.get('api/getimages/'+ cateId).then( result => {
                if(result.data.status === 0){
                    this.photoList = result.data.message;
                }
            })
        } */
        async getPhotoList(cateId){
            const {data} = await this.$http.get('api/getimages/'+ cateId);
            if(data.status === 0){
                this.photoList = data.message;
            }
        }
    },
}
</script>

<style lang="less" scoped>

*{
    touch-action: pan-y;
}



.photo-list{
    padding: 0px;
    margin: 0; 
    list-style: none;  //li
    // padding-bottom: 0px;
    li{
        background-color:#ccc; //
        text-align: center;
        margin: 10px;
        box-shadow: 0 0 6px #999; //
        position: relative; //
        //
        img{
        width:100%;
        vertical-align: middle;//
        }
        //
        .info{
            text-align: left;
            background-color: rgba(0, 0, 0, 0.4); //
            position: absolute; //
            bottom: 0px;
            color: white;
            padding: 2px;
            max-height: 88px;  //

            .info-title{
                font-size: 15px;
            }

            .info-body{
                font-size: 13px;
            }

        // ,imgimage
            img[lazy=loading] {
                width: 40px;
                height: 300px;
                margin: auto;
            }
        }   
    }  
}




</style>





