<template>


    <div class="newsinfo-containners">
        <!--  -->
        <h3 class="title">{{ newsInfo.title }}</h3>
        <!--  -->
        <p class="subtitle">
            <span>{{ newsInfo.add_time | dateFormat }}</span>
            <span>{{newsInfo.click}}</span>
        </p>

        <hr>
        <!--  -->
        <div class="content" v-html="newsInfo.content">
            <!-- HTML -->
        </div>

        <!--  -->
        <!-- <router-view></router-view> -->
        <!--  -->
        <comment :parentId="this.id"></comment>
    </div>
</template>

<script>

import { Toast } from 'mint-ui'
import comment from '../subcomponent/comment'
import router from '../../router'

export default {
    name:'newsInfo',
    data(){
        return {
            id: this.$route.params.id,//ID
            newsInfo: [],
        }
    },
    created(){
        this.getNewsInfo();
    },
    methods:{
        /* getNewsInfo(){
            this.$http.get('api/getnew/'+this.id).then( result => {
                // console.log(result.body);
                if(result.body.status === 0){
                   this.newsInfo = result.body.message[0]; //message
                }else{
                    Toast('');
                }
            } )
        } */
        async getNewsInfo(){
            const {data} = await this.$http.get('api/getnew/'+this.id);
            // console.log(data);
            if(data.status === 0){
                   this.newsInfo = data.message[0]; //message
                }else{
                    Toast('');
                }
        }
    },
    // router,
    //
    components:{
        comment
    }
}
</script>

<style lang="less">
.newsinfo-containners{
    // padding: 0 5px;//
    .title{
       font-size: 16px;
       text-align: center;
       margin: 15px 0; //margin
       padding: 0 5px
    }
    .subtitle{
        font-size: 13px;
        //
        display: flex;
        justify-content: space-between;
        padding: 0 5px;
    }
    .content{
        padding: 0 5px
    }

}

</style>





