import moment from 'moment'
//
Vue.filter('dateFormat', function (dataStr, pattern = 'YYYY-MM-DD HH:mm:ss') {
  return moment(dataStr).format(pattern)
})


//vue-preview
import VuePreview from 'vue-preview'
Vue.use(VuePreview)


//cart
var cartList = JSON.parse(localStorage.getItem('cart') || "[]");
// var cartList = JSON.parse("[]");
// localStorage.setItem('cart', JSON.stringify(cartList));



//vuex
import Vuex from 'vuex'
Vue.use(Vuex)
const store = new Vuex.Store({
  state: {
    cart: cartList,
  },
  mutations: {
    addtoCart(state, goods){  //state
      //id
      var flag = false;
      state.cart.some( (item,i) => {
        if(item.id === goods.id){
          item.count = parseInt(item.count) + parseInt(goods.count);
          item.selected = true;
          // var newitem = state.cart.splice(i,1);
          // state.cart.unshift(newitem);
          flag = true;
          return true;
        }
      })
      if( !flag ) {
        state.cart.push(goods);
      }
      console.log(state.cart);
      localStorage.setItem('cart', JSON.stringify(state.cart));
    },
    updateGoodsInfo(state, goodsInfo){   //
      state.cart.some(item => {
        if(item.id == goodsInfo.id){
          item.count = parseInt(goodsInfo.count);
          return true; //
        }
      })
      //
      localStorage.setItem('cart', JSON.stringify(state.cart));
    },
    delFormCart(state, id) {
      state.cart.some( (item, i) => {
        if (item.id == id) {
          state.cart.splice(i, 1);
          return true;
        }
      })  
      //
      localStorage.setItem('cart', JSON.stringify(state.cart));
    },
    updateGoodsSelected(state, goodsInfo){   //
      state.cart.some(item => {
        if(item.id == goodsInfo.id){
          item.selected = goodsInfo.selected;
          return true; //
        }
      })
      //
      localStorage.setItem('cart', JSON.stringify(state.cart));
    },
  },
  getters: {
    getAllCount(state) {
      var totalNum = 0;
      state.cart.forEach( item => {
      totalNum += item.count;
      })
      return totalNum;
    //   setInterval(
    //     () => {return totalNum},1000
    //  )
    },

    getGoodsCount(state) {
        var o = {} ;
        state.cart.forEach(item => {
          o[item.id] = item.count;
        })
        return o;
    },
    getGoodsSelected(state) {
      var o = {} ;
      state.cart.forEach(item => {
        o[item.id] = item.selected;
      })
      return o;
    },
    //
    getAllCountAmount(state){
      var o = { count:0, amount:0 };
      state.cart.forEach( item => {
        if(item.selected){
        o.count = o.count + parseInt(item.count);
        o.amount = o.amount + parseInt(item.price) * parseInt(item.count);
        }
      })
      return o;
    }
  }
})


import './lib/CSS/global.css'

// import 'viewerjs/dist/viewer.css'

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,  //vuex
  components: {
    App
  },
  template: '<App/>'
})



