<template>

  <div id="app" class="app-container">
		
		<!-- -->
    
    <mt-header fixed title="my-personal-vue-project">
      <span slot="left" @click="goBack()">
        <mt-button icon="back" v-show="flag"></mt-button>
      </span>
    </mt-header>

    <!--  -->
    <!-- <header id="header" class="mui-bar mui-bar-nav">
			<h1 class="mui-title">My VUE Project</h1>
      <span class=" mui-icon mui-icon-left-nav" v-show="flag" @click="goBack()"></span>
		</header> -->

    
		<!-- <img src="./assets/img/menu1.png"> -->


  <!-- router -->
	 <!-- out-in  -->	
	 	<!-- <transition mode="out-in"> -->
		<transition>
			<router-view/>
		</transition>
  


	<!--  -->
		<nav class="mui-bar mui-bar-tab">
				<router-link class="mui-tab-item-al" to="/home">
					<span class="mui-icon mui-icon-home"></span>
					<span class="mui-tab-label"></span>
				</router-link>
        <router-link class="mui-tab-item-al" to="/member">
            <span class="mui-icon mui-icon-contact"></span>
            <span class="mui-tab-label"></span>
        </router-link>
        <router-link class="mui-tab-item-al" to="/cart">
						<span class="mui-icon mui-icon-extra mui-icon-extra-cart">
              <span class="mui-badge" id="badge"> {{ this.$store.getters.getAllCount }}</span></span>
            <span class="mui-tab-label"></span>
        </router-link>
        <router-link class="mui-tab-item-al" to="/search">
            <span class="mui-icon mui-icon-search"></span>
            <span class="mui-tab-label"></span>
        </router-link>
    </nav>
		<!-- <tabbar/> -->

  </div>
</template>

<script>

export default {
  name: 'App',
  data(){
    return{
      flag: false,
    }
  },
  created(){
    this.flag = this.$route.path === "/home"? false:true;
  },
  methods:{
    goBack(){
      this.$router.go(-1);
    }
  },
  watch:{
    //URLhome
    "$route.path": function(newVal){
      if(newVal === "/home"){
        this.flag = false;
      } else {
        this.flag = true;
      }
    }
  }
}
</script>

<style>
/*  */
.mint-header.is-fixed{
	z-index: 99;
}

.app-container{
	padding-top: 40px;
	overflow-x: hidden;
	/* overflow hidden
	y */
	padding-bottom: 50px;
}

/*  */
.v-enter {
	opacity: 0;
	transform: translateX(100%);  /*100%*/
	/* position: absolute; */
}

 .v-leave-to {
	opacity: 0;
	transform: translateX(-100%); 
	position: absolute;
	/* mode:out-in */

}

.v-enter-active,.v-leave-active{
	transition: all 0.3s ease;
}


.mui-bar{
  z-index: 98;
}


/* MUIbug */

.mui-bar-tab .mui-tab-item-al.mui-active {
    color: #007aff;
}

.mui-bar-tab .mui-tab-item-al {
    display: table-cell;
    overflow: hidden;
    width: 1%;
    height: 50px;
    text-align: center;
    vertical-align: middle;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #929292;
}

.mui-bar-tab .mui-tab-item-al .mui-icon {
    top: 3px;
    width: 24px;
    height: 24px;
    padding-top: 0;
    padding-bottom: 0;
}

.mui-bar-tab .mui-tab-item-al .mui-icon~.mui-tab-label {
    font-size: 11px;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
}

</style>



