<template>
  <div class="shopcar-container">
  
    <div class="goods-list">

      <div class="mui-card" v-for="(item, i) in goodslist" :key="item.id">
				<div class="mui-card-content">
					<div class="mui-card-content-inner goods-item">
            <!--  -->
						<mt-switch v-model="$store.getters.getGoodsSelected[item.id]" @change="changeSelected(item.id, $store.getters.getGoodsSelected[item.id])" ></mt-switch>
            <!--  -->
            <img :src="item.thumb_path" alt="">
            <!--  -->
            <div class="info">
              <h1>{{ item.title }}</h1>
              <div class="goods-info">
                <span class="price">{{ item.sell_price }}</span>
                <!-- countObj[item.id]  -->
      <!--  :
      idcount
      {88:2 89:1 ...}
      -->
                <!-- <a>{{ $store.getters.getGoodsCount[item.id] }}</a> -->
                <numbox :initcount="$store.getters.getGoodsCount[item.id]" :goodsId="item.id"></numbox>
                <a href="#" @click.prevent="del(item.id, i)"></a>
              </div>
            </div>
					</div>
				</div>
			</div>

      <!--  -->
      <div class="mui-card">
				<div class="mui-card-content">
					<div class="mui-card-content-inner jiesuan">
						<div class="left">
              <p></p>
              <p>
                <span class="danger">{{ $store.getters.getAllCountAmount.count }}</span>
                <span class="danger">{{ $store.getters.getAllCountAmount.amount }}</span>
              </p>
            </div>
            <mt-button type="danger"></mt-button>
					</div>
				</div>
			</div>

      <!-- <p>{{ $store.getters.getGoodsSelected }}</p> -->

    </div> 
  </div>
</template>

<script>
// import { mapGetters, mapMutations } from "vuex";
import numbox from "../../components/subcomponent/shopcart_numbox"

export default {
     name: "cart",
    data() {
        return {
        goodslist: [] // 
        };
    },
    created() {
        this.getGoodsList();
    },
    methods: {
      async getGoodsList(){
        //storeid
        var idArr = [];
        this.$store.state.cart.forEach(item => {
          idArr.push(item.id);
        });
        //
        if( idArr.length <= 0 ){
            return;
          }
        const {data} = await this.$http.get('api/goods/getshopcarlist/'+ idArr.join(","));
          if(data.status === 0){
            this.goodslist = data.message;
            // console.log(this.goodslist);
          }      
      },
      del(id, index){
        //storeidgoodlistindex
        this.goodslist.splice(index, 1); //goodslist
        this.$store.commit("delFormCart", id);
      },
      changeSelected(goodsId, selected){
        // console.log(goodsId + "---" + selected);
        this.$store.commit("updateGoodsSelected", { id: goodsId, selected: selected })
      }
    },
      
    components: {
        numbox
    }
}
</script>
 

<style lang="less" scoped>

.shopcar-container {
  background-color: #eee;
  overflow: hidden;
  .goods-item {
    display: flex;
    img {
      width: 60px;
      height: 60px;
    }
    h1 {
      font-size: 13px;
    }
    .info {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .goods-info {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .price {
          color: red;
          font-weight: bold;
          font-size: 16px;
        }
      }
    }
  }
}
.danger {
  color: red;
  font-weight: bold;
  font-size: 16px;
}
.jiesuan {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

</style>


