<template>
   <div>

           <!--  -->
            <transition   
            @before-enter="beforeEnter"
            @enter="enter"
            @after-enter="afterEnter">
                <div class="ball" v-show="ballShow" ref="ball"></div>
            </transition>  


        <div class="goodsInfo-container">
            <!--  -->
            <div class="mui-card">
                <div class="mui-card-content">
                    <div class="mui-card-content-inner">
                        <swiper :lunbotuList="photolunbotuList" :isfull="false"></swiper>
                    </div>
                </div>
            </div>

            <!--  -->
            <div class="mui-card">
                <div class="mui-card-header">{{ goodsInfo.title }}</div>
                <div class="mui-card-content">
                    <div class="mui-card-content-inner">
                        <p class="pri">
                            <del>{{ goodsInfo.market_price }}</del>&nbsp;&nbsp;
                            <span class="now_price">{{ goodsInfo.sell_price }}</span>
                        </p>
                        <p>
                            
                            <numbox @changeNum="changeCount" :max="goodsInfo.stock_quantity"></numbox>
                 <!--    max  -->
              <!--  goodsinfo  Ajax Ajax  -->
              <!--  bobox   Ajax  
               goodsinfo   stock_quantity  undefined  -->
                        </p>
                        <p>
                            <mt-button type="primary" size="small"></mt-button>
                            <mt-button type="danger" size="small" @click="addCart" ></mt-button>
                        </p>
                    </div>
                </div>
            </div>

            <!--  -->
            <div class="mui-card">
                <div class="mui-card-header"></div>
                <div class="mui-card-content">
                    <div class="mui-card-content-inner">
                        <p>{{ goodsInfo.goods_no }}</p>
                        <p>{{ goodsInfo.stock_quantity }}</p>
                        <p>{{ goodsInfo.add_time | dateFormat }}</p>
                    </div>
                </div>
                <div class="mui-card-footer">
                    <mt-button type="primary" size="large" plain @click="goDesc(id)"></mt-button>
                    <mt-button type="danger" size="large" plain @click="goComment(id)"></mt-button>
                </div>
            </div>
       </div>  

        <!--  -->
       <router-view></router-view>     
       
    </div>
       


</template>

<script>

// 
import swiper from '../subcomponent/swiper'
import numbox from '../subcomponent/goodsinfo_numbox'
import router from '../../router/index.js'

export default {
    name: 'goodsInfo',
    data(){
        return{
            id: this.$route.params.id,
            photolunbotuList: [],
            goodsInfo: [],
            goodsDesc: [],
            ballShow: false, //
            cartNumber: 1, //
            countMax:"", //
        }
    },
    components:{
        swiper, numbox
    },

    created(){
        this.getGoodsLunbotu();
        this.getGoodsInfo();
    },
    methods:{
        /* getGoodsLunbotu(){
            this.$http.get('api/getthumimages/'+ this.id).then(
                result => {
                    if (result.body.status === 0) {
                        // console.log(result.body.message);
                        result.body.message.forEach(element => {
                            element.img = element.src;
                        });
                        //itemimgURLitem.img
                        this.photolunbotuList = result.body.message;
                    }
            })
        }, */
        async getGoodsLunbotu(){
            const {data} = await this.$http.get('api/getthumimages/'+ this.id);
            if (data.status === 0) {
                // console.log(result.body.message);
                data.message.forEach(element => {
                    element.img = element.src;
                });
                //itemimgURLitem.img
                this.photolunbotuList = data.message;
            }
        },
       /*  getGoodsInfo(){
            //
            this.$http.get('api/goods/getinfo/'+this.id).then(
                result=>{
                if (result.body.status === 0) {
                    this.goodsInfo = result.body.message[0];
                    // console.log(this.goodsInfo);
                }
            })  
        }, */
        async getGoodsInfo(){
            //
            const{data} = await this.$http.get('api/goods/getinfo/'+this.id);
            if (data.status === 0) {
                this.goodsInfo = data.message[0];
            } 
        },

        goDesc(id){
    //  
      //   this  this.$route  this.$router
      // this.$route 
      // this.$router 
            this.$router.push({ name:"goodsdesc", params:{id} });
        },
        goComment(id){
            this.$router.push({ name:"goodscomment", params:{id} });
        },
        addCart(){
            this.ballShow = !this.ballShow;
            // console.log(this.cartNumber);
            var goods = { id: this.id, price: this.goodsInfo.sell_price, count: this.cartNumber, selected: true };
            this.$store.commit("addtoCart", goods);
        },
        changeCount(num){
        // 
        // console.log("count" + c);
        //   data  
            this.cartNumber = num;
        },

        //
        beforeEnter(el){
            // var ballPosition = this.$refs.ball.getBoundingClientRect();
            // el.style.transform = `translate(${ballPosition.left}px, ${ballPosition.top}px )`;
            el.style.transform = "translate( 0, 0)";
            el.style.opacity = 1; //bug

        },
        enter(el,done){
            const ballPosition = this.$refs.ball.getBoundingClientRect();
            // DOM
            // DOMdocument
            const badgePositon = document.getElementById("badge").getBoundingClientRect(); //
            const xDist = badgePositon.left - ballPosition.left;
            const yDist = badgePositon.top - ballPosition.top;
            el.offsetWidth;
            el.style.transform = `translate( ${xDist}px, ${yDist}px )`;
             //  http://cubic-bezier.com/#.46,-0.4,1,.49
            el.style.transition = "all 0.5s cubic-bezier(.46,-0.4,1,.49)";
            done();
        },
        afterEnter(el){
            this.ballShow = !this.ballShow;
        }
    }
}
</script>

<style lang="less" scoped>

.goodsInfo-container{
    z-index: 97;
    background-color: #eee;
    overflow: hidden;   //padding

    .now_price{
        font-size: 16px;
        color: red;
        font-weight: bold;
    }
}

.mui-card-footer, .mui-card-header{
    // display: block;
    button{
        margin: 10px 2px;
    }
}

// 
.ball{
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: red;
    position: absolute;  //
    z-index: 99;
    left: 148px;
    top: 340px;
}



// Bug
//   
//  translate   
//    
//  + 
//  + 
//  +   -  

</style>





