<template>
    <div>

        <!--  -->
        <button class="mui-btn  commentBtn" @click="show = true">
        <!-- <button class="mui-btn  mui-btn-block mui-btn-outlined commentBtn" @click="show = true"> -->
            
        </button>
        <div id="picture" class="mui-popover mui-popover-action mui-popover-bottom mui-active commenttabel" v-show="show" >
			
            <div class="mui-table-view-div">
                <span class="mui-table-view-span"  @click="quitComment"></span>
                <span class="mui-table-view-span" @click="postComment"></span>
			</div>

            <textarea placeholder="" v-model="newcomment"></textarea>
		</div>

        <hr style="border-weight:800">

        <h3></h3>
                <!--  -->   
        <div class="comment-container">  
            <div class="commentList" v-for="(item, i) in commentList" :key="item.add_time">
                <div class="commentList-left">
                    <span class="mui-icon mui-icon-contact mui-icon-icon-contact-filled"></span>
                    <span class="commentList-floor">{{i+1}}</span>
                </div>
                <div class="commentList-commentBox" >
                    <p>
                    <span class="commentList-username">{{item.user_name}}</span>
                    <span class="commentList-time">{{ item.add_time | dateFormat }}</span>                        
                    </p>

                    <p class="commentList-contentBox">{{item.content}}</p>
                </div>
            </div>

            <!-- key key 
             Vue  Vue  -->
            <transition mode="out-in">
            <button type="button" class="mui-btn mui-btn-outlined mui-btn-primary" 
            style='width:100%' v-if="more_show" @click="getmoreInfo" :key="'more'">
            
            </button>
            <button type="button" class="mui-btn mui-btn-outlined mui-btn-primary mui-disabled" 
            disabled="" style='width:100%' v-else="more_show"><span class="mui-spinner" :key="'loading'">
                </span>&nbsp;<span>Loading...</span>
                </button>
            </transition>
        </div>  
        <!--  -->
        <div class="mui-backdrop mui-active mui-backdrop-action" v-show="show"></div>



    </div>
</template>

<script>

import { Toast } from 'mint-ui'


export default {
    name:'comment',
    data(){
        return{
            show: false,
            pageIndex: 1,
            commentList: [],
            more_show: true,
            newcomment: '',  //
            id: this.$route.params.id,
        }
    },
    props:['parentId'],
    created(){
        this.getNewsInfo();
    },
    methods:{
        //
        //vue-resourceaxios
    /*    getNewsInfo(){
            this.$http.get('api/getcomments/' + this.id + "?pageindex=" + this.pageIndex )
            .then( result => {
                if( result.body.status === 0 ){
                    // console.log(result.body.methods);
                    this.commentList = this.commentList.concat(result.body.message);
                    // this.more_show = true;
                }else{
                    Toast('');  
                    // this.more_show = true;
                }
            }) 
        },*/
        async getNewsInfo(){
            const {data} = await this.$http.get('api/getcomments/' + this.id + "?pageindex=" + this.pageIndex);
            // console.log(data);
            if( data.status === 0 ){
                this.commentList = this.commentList.concat(data.message);
            }else{
                Toast('');
            }
        },


        //
        getmoreInfo(){
            this.pageIndex++;
            this.more_show = false;
            this.getNewsInfo();
            setInterval( ()=>{ this.more_show = true;}, 1000 );
        },
        //
      /*   postComment(){
            //
            //trim() trim() 
            if( this.newcomment.trim().length === 0 ){
                Toast('');
                return;
            }else{
            //post1URL 
            //2. { content:this.msg }
            // 3. { emulateJSON:true }
             // this.$http.post( 'api/postcomment/' + $route.params.id  )//idid
                this.$http.post( 'api/postcomment/' + this.id, { content:this.newcomment.trim()  })
                .then(function(result){
                    if(result.body.status === 0){
                        Toast('');
                        //
                        var cmt = { user_name:'', add_time: Date.now(), content: this.newcomment };
                        this.commentList.unshift(cmt);
                        this.show = false;
                         this.newcomment = "";
                    }else{
                        Toast('');
                    }
                })
            } 
        },*/
        async postComment(){
            if( this.newcomment.trim().length === 0 ){
                Toast('');
                return;
            }else{
                const {data} = await this.$http.post( 'api/postcomment/' + this.id, { content:this.newcomment.trim() });
                // console.log(data);
                if(data.status === 0){
                    Toast('');
                    //
                    var cmt = { user_name:'', add_time: Date.now(), content: this.newcomment };
                    this.commentList.unshift(cmt);
                    this.show = false;
                        this.newcomment = "";
                }else{
                    Toast('');
                }
            }
        },
        quitComment(){
            this.show = false;
            this.newcomment = "";
            }
    },

}



</script>

<style lang="less" scoped> 

//
.comment-container{
    padding-bottom: 40px;
}

//
.commentBtn{
    // padding: 5px 20px;
     color:rgba(255, 1, 1, 0.39); 
     font-size:16px; 
     font-weight: 700;
     background-color:rgb(218, 219, 221); 
     position:fixed;
     width: 100%;
    //  margin-bottom: 50px;
    //  padding-bottom: 50px;
     bottom:50px;
     z-index: 3;
}

//
h3{
    margin-top:10px;
    margin-bottom: 15px;
    margin-left:10px;
    font-size: 18px;
    font-weight: 800;
}

//
.commentList{
    display: flex;
    padding: 5px 10px;
    justify-content:flex-start;
    padding-bottom: 15px;
    margin:5px;

    .commentList-left{       
         width:12%;        
        //  margin-top: 2px;
         text-align:center ;
        //
        .mui-icon-icon-contact-filled{
        color: darkgray;
        width: 100%;
        font-size:40px;
        // flex:1;
        }
        .commentList-floor{
            font-size:11px;
        }  
    }
}

 .commentList-commentBox{
    padding: 2px 5px;            
    font-family: "";
    font-weight: bold;
    color:black;
    .commentList-username{
        font-size: 15px;
        color:rgb(231, 132, 99);
        margin:2px 0;
    }
    .commentList-contentBox{
        font-size: 14px;
        margin:0;
    }
    .commentList-time{
        font-size: 10px;
        // text-align: right;
        color: rgba(156, 176, 199, 0.979);
        margin-left:10px;
    }
}



//
.mui-popover{
    height: 90px;
    // z-index: 100;
    // background-color: white;
}
.mui-popover.mui-popover-action{
    // width: 98%;
}

.commenttabel{
    margin-bottom: 0px;
     background-color:white !important;
    //  width:100%;
     border-radius: 3px;
     padding-top: 5px;
    //  padding-bottom:20px;
}
.mui-table-view{
    display: flex;
    // padding: 5px 5px;
    }
 .mui-table-view-div{
    display: flex;
    justify-content: space-between;
    margin: 8px 10px;
    // background-color:#eae8e8;
    .mui-table-view-span{
    padding: 0 7px;
    font-size: 16px;
    font-weight: 500;
    color: rgb(91, 91, 92);
} 
}
textarea{
    width: 97.5%;
    height:40px;
    background-color:#f0eeee;
    margin:0 10px;
    margin-bottom: 10px;
    margin-left: 5px;
    margin-right: 5px;
    // margin: 5px 20px 5px 5px ;
    font-size:14px;
    line-height: 30px;  
}


      //placeholder
 textarea::-webkit-input-placeholder{
    font-size: 15px;
    line-height: 25px;
    vertical-align: middle;  
}
.v-enter, .v-leave-to{
    opacity: 0;
    position:relative;
}

.v-enter-active, .v-leave-active{
    transition: opacity 0.1s;
}


</style>





